import React, { Component } from 'react';

import Container from 'react-bootstrap/Container';
import Alert from 'react-bootstrap/Alert';
import Navbar from 'react-bootstrap/Navbar';
import Nav from 'react-bootstrap/Nav';
import Badge from 'react-bootstrap/Badge';
import Modal from 'react-bootstrap/Modal';

import axios from 'axios';

// Custom components for this application
import QuoteDisplay from '../QuoteDisplay/QuoteDisplay';
import MessageDisplay from '../MessageDisplay/MessageDisplay';
import SymbolExchangeInput from '../SymbolExchangeInput/SymbolExchangeInput';
import SymbolExchangeInputEnabler from '../SymbolExchangeInputEnabler/SymbolExchangeInputEnabler';
import Note from '../Note/Note';

import {
   AreaChart,
   Area,
   XAxis,
   YAxis,
   CartesianGrid,
   Tooltip,
   ReferenceLine,
   ResponsiveContainer,
   Label
} from "recharts";


require('dotenv').config();

/**
 * App should render the main content/component/container for an application
 */
class App extends Component {
   constructor(props) {
      super(props);
      this.state = {
         latestQuotes: [],
         "errorMessage": null,
         'message': null,
         showSymbolExchangeInput: true,
         showChart: false,
         chart: null
      }
   }

   updateMessage = (errMsg, msg) => {
      this.setState({ "errorMessage": errMsg, 'message': msg });
   }


   updateQuotes = (quote) => {
      let updatedQuotes = [...this.state.latestQuotes];
      const quoteNums = this.state.latestQuotes.length;
      let foundMatching = false;
      for (let index = 0; index < quoteNums; index++) {
         let row = updatedQuotes[index];
         if (row.symbol === quote.symbol) {
            foundMatching = true;
            row.marketState = quote.marketState;
            row.regularMarketDayHigh = { ...quote.regularMarketDayHigh };
            row.regularMarketDayLow = { ...quote.regularMarketDayLow };
            row.regularMarketChangePercent = { ...quote.regularMarketChangePercent };
            row.regularMarketChange = { ...quote.regularMarketChange };
            row.regularMarketPrice = { ...quote.regularMarketPrice };
            row.regularMarketTime = { ...quote.regularMarketTime };
            if ((quote.marketState === "POST" || quote.marketState === "POSTPOST" || quote.marketState === "PREPRE") && quote.postMarketTime) {
               row.postMarketPrice = { ...quote.postMarketPrice };
               row.postMarketTime = { ...quote.postMarketTime };
               row.postMarketChangePercent = { ...quote.postMarketChangePercent };
               row.postMarketChange = { ...quote.postMarketChange }
            } else if (quote.marketState === "PRE" && quote.preMarketTime) {
               row.preMarketPrice = { ...quote.preMarketPrice };
               row.preMarketTime = { ...quote.preMarketTime };
               row.preMarketChangePercent = { ...quote.preMarketChangePercent };
               row.preMarketChange = { ...quote.preMarketChange }
            }
            break;
         }
      }

      if (!foundMatching) {
         updatedQuotes = [quote, ...updatedQuotes];
      }

      this.setState({ latestQuotes: updatedQuotes });
   }

   getQuoteChart = (symbol, exchange, show, showChart) => {


      //console.log("index.js - symbol: " + symbol);
      //console.log("index.js - exchange: " + exchange);
      const yahooSymbol = exchange === "tsx" ? symbol + ".to"
         : (exchange === "cse" ? symbol + ".cn"
            : (exchange === "tsxv" ? symbol + ".v"
               : (exchange === "neo" ? symbol + ".ne" : symbol)));

      if (showChart) {
         this.setState({
            showSymbolExchangeInput: show, showChart: showChart
         });

         axios.get(process.env.REACT_APP_CORS_PROXY + '/https://query1.finance.yahoo.com/v8/finance/chart/' + yahooSymbol + '?includePrePost=false&interval=2m&useYfid=true&range=1d')
            .then(res => {
               //console.log(res);
               const chartArray = res.data.chart.result;
               //console.log(quotes);
               if (chartArray && chartArray.length > 0) {
                  //console.log("Got last price ... ");
                  const timeStamps = chartArray[0].timestamp;
                  //console.log(message);
                  if (timeStamps && timeStamps.length > 0) {
                     this.setState({
                        chart: { exchange: exchange, symbol: symbol, x: timeStamps, y: chartArray[0].indicators.quote[0].close, previousClose: chartArray[0].meta.previousClose },
                        errorMessage: null
                     });
                  } else {
                     this.setState({ chart: null, "errorMessage": "Sorry, no data found." });
                  }
               } else {
                  if (res.data.chart.error) {
                     const code = res.data.chart.error.code;
                     const description = res.data.chart.error.description;
                     this.setState({ chart: null, "errorMessage": code + ": " + description });
                  } else {
                     this.setState({ chart: null, "errorMessage": "Sorry, no data found." });
                  }
               }
            })
            .catch(err => {
               //console.log(err);
               let message;
               if (err.response && err.response.data) {
                  if (typeof err.response.data === 'string') {
                     message = err.response.data.includes('Cannot GET /quote') ? 'The API backend is not available. Please have a check.' : 'HTTP ' + err.response.status + ': ' + err.response.data;
                  } else {
                     message = err.response.data.name + ": " + err.response.data.message;
                  }
                  this.setState({ chart: null, "errorMessage": message });
               } else {
                  message = err.name + ' (' + err.code + '): ' + err.message;
                  this.setState({ chart: null, "errorMessage": message });
               }
            });
      } else {
         this.setState({ showSymbolExchangeInput: show, showChart: false });
         axios.get(process.env.REACT_APP_CORS_PROXY + '/https://query1.finance.yahoo.com/v7/finance/quote?formatted=true&symbols=' + yahooSymbol)
            .then(res => {
               //console.log(res);
               const quotes = res.data.quoteResponse.result;
               //console.log(quotes);
               if (quotes && quotes.length > 0) {
                  //console.log("Got last price ... ");
                  const message = quotes[0].longName ? quotes[0].longName + ' on ' + quotes[0].fullExchangeName
                     : (quotes[0].shortName ? quotes[0].shortName + ' on ' + quotes[0].fullExchangeName : null);
                  //console.log(message);
                  if (message) {
                     this.setState({ "errorMessage": null, 'message': message, chart: null });
                     this.updateQuotes(quotes[0]);
                  } else {
                     this.setState({ "errorMessage": "Sorry, no data found.", chart: null });
                  }
               } else {
                  this.setState({ "errorMessage": "Sorry, no data found.", chart: null });
               }
            })
            .catch(err => {
               //console.log(err);
               let message;
               if (err.response && err.response.data) {
                  // The request was made and the server responded with a status code
                  // that falls out of the range of 2xx
                  //console.log(err.response.data);
                  //console.log(err.response.status);
                  //console.log(err.response.headers);
                  if (typeof err.response.data === 'string') {
                     message = err.response.data.includes('Cannot GET /quote') ? 'The API backend is not available. Please have a check.' : 'HTTP ' + err.response.status + ': ' + err.response.data;
                  } else {
                     message = err.response.data.name + ": " + err.response.data.message;
                  }
                  this.setState({ "errorMessage": message, chart: null });
               } else {
                  message = err.name + ' (' + err.code + '): ' + err.message;
                  this.setState({ "errorMessage": message, chart: null });
               }
            });
      }
   }

   toggleSymbolExchangeInputShow = () => {
      this.setState(state => ({
         showSymbolExchangeInput: !state.showSymbolExchangeInput
      }));
   }

   handleCloseChart = () => {
      this.setState({ showChart: false, chart: null });
   }
   
   render() {

      // prepare data in chart
      const quotes = this.state.chart && this.state.chart.y.filter(value => value !== null)
      .map((value, index) => ({
         time: new Date(this.state.chart.x[index] * 1000).toLocaleTimeString("en-CA", {
            hour12: false,
            hour: "2-digit",
            minute: "2-digit"
         }),
         quote: Math.round(value * 100) / 100
      }));
      const previousClose = this.state.chart && this.state.chart.previousClose;

      // four ticks as in Yahoo! Finance
      const tick1 = this.state.chart && new Date((this.state.chart.x[0] + 1800) * 1000).toLocaleTimeString("en-CA", {hour12: false, hour: "2-digit", minute: "2-digit"});
      const tick2 = this.state.chart && new Date((this.state.chart.x[0] + 1800 + 3600 * 2) * 1000).toLocaleTimeString("en-CA", {hour12: false, hour: "2-digit", minute: "2-digit"});
      const tick3 = this.state.chart && new Date((this.state.chart.x[0] + 1800 + 3600 * 4) * 1000).toLocaleTimeString("en-CA", {hour12: false, hour: "2-digit", minute: "2-digit"});
      const tick4 = this.state.chart && new Date((this.state.chart.x[0] + 1800 + 3600 * 6) * 1000).toLocaleTimeString("en-CA", {hour12: false, hour: "2-digit", minute: "2-digit"});

      return (
         <Container>
            <Navbar bg="dark" variant="dark" sticky="top" expand="sm">
               <Navbar.Brand>
                  <span role="img" aria-label="Stock">&#x1F4C8;</span> <a href="https://enjoyit.ca" title="go to enjoyit.ca">enjoyit.ca</a> <Badge variant="light">v{process.env.REACT_APP_VERSION}</Badge>
               </Navbar.Brand>
               <Navbar.Toggle aria-controls="responsive-navbar-nav" />
               <Navbar.Collapse id="responsive-navbar-nav" className="justify-content-end">
                  <Nav>
                     <Nav.Link href="mailto:feedback@enjoyit.ca" title="provide feedback to feedback@enjoyit.ca">Feedback</Nav.Link>
                     <Nav.Link href="mailto:donations@enjoyit.ca" title="eTransfer less than $10 to donations@enjoyit.ca">Donations</Nav.Link>
                  </Nav>
               </Navbar.Collapse>
            </Navbar>
            <Note />
            {this.state.showSymbolExchangeInput ? <SymbolExchangeInput handleSubmit={this.getQuoteChart} handleInputChange={this.updateMessage} />
               : <SymbolExchangeInputEnabler onClick={this.toggleSymbolExchangeInputShow} />}
            {this.state.message && <MessageDisplay msg={this.state.message} />}
            {this.state.errorMessage && <Alert variant='warning'>{this.state.errorMessage}</Alert>}
            {this.state.latestQuotes.length > 0 && <QuoteDisplay data={this.state.latestQuotes} handleSubmit={this.getQuoteChart} />}
            {this.state.chart &&
               <Modal show={this.state.showChart} onHide={this.handleCloseChart}>

                  <Modal.Body>
                     <p className="text-center">{this.state.chart.symbol.toUpperCase()} on {this.state.chart.exchange.toUpperCase()} Exchange <br />
                        <small>{new Date(this.state.chart.x[0] * 1000).toLocaleDateString('en-CA')} {new Date(this.state.chart.x[0] * 1000).toLocaleTimeString('en-CA', { hour12: false, hour: '2-digit', minute: '2-digit' })} - {new Date(this.state.chart.x.at(-1) * 1000).toLocaleTimeString('en-CA', { hour12: false, hour: '2-digit', minute: '2-digit' })}</small></p>
                     <ResponsiveContainer width={'90%'} height={300}>
                        <AreaChart width={500} height={400} data={quotes} className="my-3 mx-3">
                           <CartesianGrid strokeDasharray="3 3" />
                           <XAxis dataKey="time" tickLine={false} tickSize={8} axisLine={false} tick={{ fontSize: '0.8rem' }} ticks={[tick1, tick2, tick3, tick4]} />
                           <YAxis type="number" domain={["auto", "auto"]} orientation='right' tickLine={false} tickSize={0} axisLine={false} tick={{ fontSize: '0.8rem' }} />
                           <Tooltip formatter={(value, name, props) => [value, ""]} separator={''} contentStyle={{ fontSize: '0.8rem' }} content={({ active, payload, label }) => {
                              if (active && payload && payload.length) {
                                 return (
                                    <div className="custom-tooltip" style={{ backgroundColor: '#ddd', fontSize: '0.8rem', padding: '0 0.5rem' }}>
                                       <p className="label">{`${label} - ${payload[0].value}`}</p>
                                    </div>
                                 );
                              } else { return <></> }
                           }} />
                           <ReferenceLine
                              y={previousClose}
                              stroke="black"
                              strokeDasharray="3 3"
                              isOverflow='extendDomain'
                           >
                              <Label value={previousClose} position={"bottom"} fontSize={'0.8rem'} fontWeight={'bold'} />
                           </ReferenceLine>

                           <Area type="monotone" dataKey="quote" stroke="#8884d8" fill={Math.round(this.state.chart.y.at(-1) * 100) / 100 > previousClose ? "green" : "red"} />
                        </AreaChart>
                     </ResponsiveContainer>
                  </Modal.Body>
               </Modal>}
         </Container>
      );
   }
}

export default App;